.mob-menu {
  z-index: 99;
  width: 100vw;
  height: 100%;
  opacity: 1;
  background-color: #e17992;
  padding-top: 58px;
  padding-bottom: 58px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  transform: translateY(0);
}

